
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


const emptyObject = {
name: '',
newname: '',
mobile: '',
newmobile: '',
vcode: '',
oldpassword: '',
newpassword: '',
newpassword1: ''
}

export default{
mixins: [common,globals,wxjssdk],
components: {
Cell,
Popup,
Group,
XInput,
XButton
},
data () {
return {
popup_setting: false,
action: '',
tId: null,
send: {
text: '发送验证码',
loading: false
},
user: util.copy(emptyObject)
}
},
mounted () {
this.initUser()
},
computed: {
my_menu () {
let list = this.$store.state.ui_config.my_menu
return list
},









bids () {
return this.user$.employee.bids
},
required () {
let user = this.user
if(this.action == 'changeName'){
return user.newname.length === 0
}else if (this.action == 'changeMobile'){
return !(/^1[35789]{1}\d{9}$/).test(user.newmobile)||user.vcode.length === 0
}else {
return user.oldpassword.length === 0 || user.newpassword.length === 0 || user.newpassword1.length === 0
}
},
},
methods: {
changeBranch () {
this.branch().popupShow = true
},
initUser () {
let user = this.user$
this.user.name = user.name
this.user.newname = user.name
this.user.mobile = user.mobile
this.user.is_weixin_bind = user.is_weixin_bind

},
changeAvatar () {
this.chooseImage(1,true).then(imgs=>{
this.avatarChanged(imgs[0])
})
},
toggleBindWx () {
if(this.user.is_weixin_bind){
this.unBindWX()
}else{
this.bindWX()
}
},
unBindWX () {
this.$http.post('users/'+this.user.uid+'/dounbind')
.then(response=>{
this.user.is_weixin_bind = 0
this.$store.commit('updateUserInfo',{user:this.user,action: 'unbind_wx'})
}).catch(response=>{
this.$Message.error(response.body.message)
})
},
bindWX () {
let user = this.$store.state.user
let store = this.$store
let p = this.$route.fullPath
if(this.user.is_weixin_bind == 0){
location.href = store.state.config.OAUTH_URL+'&p='+p+'&u='+store.state.user.info.uid
}
},
avatarChanged (data) {
this.user.avatar = data.file_url
this.action = 'changeAvatar'
this.edit_profile()
},
sendCapt() {
if(!this.checkTel()) {
return false
}
this.getCapt()
},
getCapt() {
let time = 59
if(this.send.loading) {
return false
}
this.send = {loading: true,text: '60s'}
this.tId = setInterval(() => {
if(time < 10) {
time = '0' + time
}
this.send.text = time + 's'
time --
if(time < 0) {
clearInterval(this.tId)
this.send = {loading: false,text: '发送验证码'}
}
}, 1000)
this.$rest('open/vcode')
.post({mobile:this.user.newmobile,type:'change'})
.success(response => {

})
.error(response => {
this.alert('获取验证码失败', response.body.message)
clearInterval(this.tId)
this.send = {loading: false,text: '发送验证码'}
})
},
checkTel() {
let tel = this.user.newmobile
if(tel === '') {
this.alert('请先输入手机号', '手机号不能为空')
return false
}
if(tel !== '' && !(/^1[35789]{1}\d{9}$/).test(tel)) {
this.alert('请重新输入手机号', '手机号格式不正确')
return false
}
return true
},
show_popup (action) {
this.action = action
this.popup_setting = true
},
hide_popup () {
this.user.newmobile = ''
this.user.newname = this.user.name
this.user.oldpassword = ''
this.user.newpassword = ''
this.user.newpassword1 = ''

this.popup_setting = false
this.send = {
text: '发送验证码',
loading: false
}
window.clearInterval(this.tId)
},
edit_profile () {
if(this.user.newpassword  === this.user.newpassword1){
let params = {action: this.action}

if(this.action == 'changeName'){
params.name = this.user.newname
}

else if(this.action == 'changeMobile'){
params.mobile = this.user.newmobile
params.vcode = this.user.vcode
}
else if(this.action == 'changeAvatar'){
params.avatar = this.user.avatar
}

else{
params.oldpassword = this.user.oldpassword
params.newpassword = this.user.newpassword
params.newpassword1 = this.user.newpassword1
}
this.$http.put('profile',params)
.then(response=>{
if(this.action == 'changeName') {
this.user.name = this.user.newname
this.$store.commit('updateUserInfo',{user:this.user,action:'changeName'})
}else if(this.action == 'changeMobile'){
this.user.mobile = this.user.newmobile
this.$store.commit('updateUserInfo',{user:this.user,action:'changeMobile'})
}else if(this.action == 'changeAvatar'){
this.$store.commit('updateUserInfo',{user:this.user,action:'changeAvatar'})
}
this.toast('修改成功','success')
this.hide_popup()
}).catch(response=>{
this.toast(response.body.message,'warn')
})
}else{
this.toast('两次密码不一致','warn')
}
},
logout () {
this.$http.get('logout')
.then(response=>{
this.$store.commit('clearLoginInfo')
this.$router.push({path: '/login'})
}).catch(response=>{
this.$Message.error(response.body.message)
})
},
}

}



